.stepper-section {
    .bx-bg-gray {
        background: #F5F5F5;
        // box-shadow: 0px 4px 4px rgb(0 0 0 / 4%);
        border: none !important;
        border-radius: 4px;
        // margin-right: -16px !important;
        padding: 10px;
        margin: 12px 0px 12px 0px;
    }

    .stepper-card-min {
        margin: 10px 248px;
        border: none;
        background-color: white;
    }

    .mt-40 {
        margin-top: 50px;
    }

    .scroll-stepper-card {
        height: 400px;
        overflow-y: scroll;
    }

    .ml-20 {
        margin-left: 20px;
        margin-right: 20px;
    }

    .stepper-card-bg-white {
        background: white;
        padding: 20px;
    }

    .scroll-bg-gray {
        background: #F8F8F8;
        padding: 15px;

    }
    .stepper-accept-btn {
        display: flex;
        justify-content: center;
    }
    .step-accept {
        font-weight: 400;
        font-size: 13px;
        line-height: 24px;
        padding: 0 40px 0 40px;
        background: #3B3364;
    }
    .stepper-terms {
        font-weight: 400;
        font-size: 11px;
        line-height: 15px;
        color: #183153;

    }
.mt-30{
    margin-top: 30px;
}

    .mt-mb-20 {
        margin-top: 20px;
        margin-bottom: 20px;
    }
    .personal-header-part {
        display: flex;
        justify-content: space-between;
        position: sticky;
        z-index: 1;
        top: 0;
        border: 1px solid #F8F0F0 !important;
        background: white;
        padding: 5px 10px 5px 20px;
    }
    .d-btn-flex {
        display: flex;
        align-items: center;
    }
    .stepper-cam-img{
        width: -webkit-fill-available;
        cursor: pointer;
    }

    
}