.cds--btn--primary:active {
  background-color: var(--cds-button-primary-active, #3B3364);
}

.cds--btn--primary:hover {
  background-color: var(--cds-button-primary-active, #3B3364);
}

.stepper-container .stepper-item .stepper-title {
  font-weight: 400;
  font-family: 'Nunito Sans' !important;
  font-size: 13px !important;
  line-height: 24px;
  color: #3B3364 !important;
}

.stepper-container .stepper-item .stepper-title-active {
  font-weight: 700;
  font-family: 'Nunito Sans' !important;
  font-size: 13px !important;
  line-height: 24px;
  color: #3B3364 !important;
}

.stepper-item-inner-active {
  background: #3B3364 !important;
}

.stepper-item-inner-completed {
  background-color: #3B3364 !important;
}

.cds--modal-header {
  border: 1px solid #E2D1D1;
  padding: 10px;
}

.color-transparent-a {
  color: transparent;
}

a.cds--side-nav__link {
  color: transparent;
}

.d-flex-justify-space {
  display: flex;
  justify-content: space-evenly;
}

.cam-modal {
  display: flex;
  justify-content: right;
  margin-top: 10px;
}

@media (min-width: 42rem) {
  .d-flex {
    justify-content: space-between !important;
    margin-right: 40px;
  }
  .stepper-modal{
    margin-left: 80px;
  }
  /* .w-92 {
    width: 92% !important;
} */
}

@media (min-width: 99rem) {
  .d-flex {
    justify-content: space-between !important;
  }
  .stepper-modal{
    margin-left: 400px;
  }
  /* .w-92 {
    width: 100% !important;
} */
  
}

.cds--modal-header {
  border: 1px solid #E2D1D1;
  padding: 10px;
}

.cds--data-table th {
  padding-right: 1rem;
  padding-left: 1rem;
  background-color: #3B3364 !important;
  color: #FCFCFC !important;
}

.cds--data-table td {
  padding-right: 1rem;
  padding-left: 1rem;
  border-top: 1px solid var(--cds-layer);
  border-bottom: 1px solid var(--cds-border-subtle);
  background: var(--cds-layer);
  color: #183153;
  font-weight: 600;
}

.cds--data-table-content {
  border: 1px solid #D9D9D9;
}

.stepper-container .stepper-item .stepper-title {
  background-color: white;
  z-index: 1;
}

.cds--text-input {
  border: 1px solid #D8D8D8 !important;
  border-radius: 4px;
  margin-bottom: 20px;
}

.cds--modal .cds--text-area {
  border: 1px solid #D8D8D8;
  border-radius: 4px;
  margin-bottom: 10px;
}

.d-flex-justify-space-btw {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.more-images {
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  color: #183153;
  width: 120px;
  background: #E3F7FF;
  border: 1px solid #D8D8D8;
  height: 100px;
  padding: 32px 12px 32px 15px;
  border-radius: 4px;
  cursor: pointer;
}

.cds--label {
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;
  color: #183153 !important;
}

.pt-40 {
  padding-top: 40px;
}
.mb-40{
  margin-bottom: 40px;
}