.personal-section {

    .btn-zone-viewdetails {
        margin-top: 8px;
    }

    .btn-zone-viewdetails:active,
    .btn-zone-viewdetails:focus {
        border: 1px solid #6247CF !important;
    }

    .cds--btn--tertiary {
        width: -webkit-fill-available;
        border-width: 1px;
        padding-left: 13px !important;
        padding-right: 13px !important;
        background: #FFFFFF !important;
        border: 1px solid #F3E7E7 !important;
        border-radius: 0px !important;
        color: #6247CF !important;
        font-weight: 700 !important;
        font-size: 11px !important;
    }



    .charts-d-flex {
        display: flex;
        justify-content: space-evenly;
    }

    .cds--chart-holder .cds--cc--toolbar {
        display: none;
    }

    .charts-head {
        //         font-weight: 500;
        // font-size: 11px;
        line-height: 24px;
        color: #183153;
        text-align: center;
    }

    #navi {
        font-family: 'Nunito Sans' !important;
        // position: sticky;
        width: 100%;
        height: 50px;
        line-height: 50px;
        // left: 40px;
        border-bottom: 1px solid #CBC4C4;
        ;

        ul {
            text-align: left;
            list-style-type: none;

            li {
                display: inline-block;

                a {
                    padding: 10px 20px 10px;
                    text-decoration: none;
                    color: #183153;

                    &:hover {
                        color: #183153;
                        font-weight: bold;
                        border-bottom: 2px solid #183153;
                    }

                    &:active {
                        color: #183153;
                        font-weight: bold;
                        border-bottom: 2px solid #183153;
                    }

                    &:focus {
                        color: #183153;
                        font-weight: bold;
                        border-bottom: 2px solid #183153;
                    }
                }
            }
        }
    }
   
    .current {
        border-bottom: 5px solid white;
    }

    .section-page {
        width: 100%;
        text-indent: 50px;
        font-weight: 700;
        padding-top: 40px;

        p {
            font-family: 'Nunito Sans' !important;

        }
    }
    .personal-tabs-fixed {
        position: fixed;
        background: white;
        z-index: 1;
        width: 100%;
    }
   .pt-210{
    padding-top: 210px;
   }




   
}